import { useCollectionData } from 'react-firebase-hooks/firestore';
import { collection } from "firebase/firestore"
import { db } from "../firebase"
import "../styles.css";

const ref = collection(db, "home");

const Languages = (isDatabase) => {
    const [data, isLoading] = useCollectionData(ref);

    if (isLoading) {
        return <div>Loading the images...</div>
    }
    else {
        // sorting based on preference
        const sortedData = data.sort((a,b) => (a.priority <= b.priority ? -1 : 1));

        if (isDatabase.isDb === "false") {
            const finalData = sortedData.filter(x => !(x.database));
            return (
                <div className='row'>
                    {finalData.map((lang) => (
                        <img
                        id={lang.id}
                        src={lang.lang}
                        className="photo"
                        alt=""
                        />
                    ))}
                </div>
            );
        } else {
            const finalData = sortedData.filter(x => (x.database));
            return (
                <div className='row'>
                    {finalData.map((lang) => (
                        <img
                        id={lang.id}
                        src={lang.lang}
                        className="photo"
                        alt=""
                        />
                    ))}
                </div>
            );
        }
    }
}

export default Languages;